<template>
  <div class="container">
    <h1>Latest Posts</h1>
    <div class="create-post">
      <label for="create-post">Say Something...</label>
      <input type="text" id="create-post" v-model="text" placeholder="Enter your text">
      <button @click="createPost">Submit!</button>
    </div>
    <hr>
    <p class="error" v-if="error">{{ error }}</p>
    <div class="spinner">
      <bounce-loader :loading="loading"></bounce-loader>
    </div>
    <div class="posts-container">
      <div
        class="post"
        v-for="post in reversePosts"
        :key="post._id"
        v-on:dblclick="deletePost(post._id)"
      >
        <div
          class="created-at"
        >{{ `${post.createdAt.getDate()}/${post.createdAt.getMonth()}/${post.createdAt.getFullYear()}` }}</div>
        <p class="text">{{ post.text }}</p>
      </div>
    </div>
  </div>
</template>

<script>
import PostService from '../PostService';
import BounceLoader from 'vue-spinner/src/BounceLoader.vue'

export default {
  name: 'PostComponent',
  components: {
    BounceLoader,
  },
  data() {
    return {
      loading: true,
      posts: [],
      error: '',
      text: '',
    }
  },
  computed: {
    reversePosts() {
      return this.posts.slice().reverse();
    }
  },
  async created() {
    try {
      this.posts = await PostService.getPosts();
      this.loading = false;
    } catch (error) {
      this.loading = false;
      this.error = error.message;
    }
  },
  methods: {
    async createPost() {
      this.loading = true;
      await PostService.insertPost(this.text);
      this.posts = await PostService.getPosts();
      this.text = '';
      this.loading = false;
    },
    async deletePost(id) {
      if (confirm('Delete?')) {
        this.loading = true;
        await PostService.deletePost(id);
        this.posts = await PostService.getPosts();
        this.text = '';
        this.loading = false;
      }
    }
  }
}
</script>

